*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  overflow-wrap: break-word;
  user-select: text;
}

body {
  box-sizing: border-box;
  margin: 0;
  background-color: gray;
  font-family: Arial, Helvetica, sans-serif;
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  outline: none;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.containers {
  max-width: 1440px;
  padding: 0 90px;
  margin: 0 auto;
  box-sizing: border-box;
}

.display-n {
  display: none !important;
}

opacity-h {
  opacity: 0 !important;
}

.header {
  width: 100%;
  min-height: 70px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header .containers {
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  width: 100%;
}

.header__block {
  width: max-content;
  display: flex;
  align-items: center;
}

.header__block:first-child {
  justify-content: flex-start;
  gap: 32px;
}

.header__block:last-child {
  justify-content: flex-end;
  gap: 30px;
}

.header__logo {
  cursor: pointer;
}

.header__subtitle {
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #262626;
}

.header__email {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 11px;
}

.header__email p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: -0.5px;
  color: #140848;
}

.header__phone {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 11px;
}

.header__phone p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  letter-spacing: -0.5px;
  color: #140848;
}

.header__button {
  background: #f2ce14;
  border-radius: 10px;
  padding: 10px 17px;
  cursor: pointer;
}

.header__button p {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #141413;
  text-transform: uppercase;
}

.headerCitySelect {
  padding: 7px 5px 7px 25px;
  border-radius: 30.5px;
  background-color: #ffffff;
  width: 75px;
  position: relative;
}

.headerCitySelect::after {
  content: "";
  background: url("./../svg/header/arrow-down-blue-dark.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: -8px;
  transform: translate(0, -50%);
  height: 11px;
  width: 14px;
}

.headerCitySelect::before {
  content: "";
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 0;
  background-image: url("./../svg/header/geo.svg");
  transform: translate(0, -50%);
}

.headerCitySelect-placeholder {
  display: block;
  padding: 5px 0;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  color: #140848;
  font-weight: 500;
  font-size: 15px;
  line-height: 19px;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  white-space: nowrap;
}

.headerCitySelect-block {
  bottom: auto;
  top: calc(100% + 3px);
  position: absolute;
  left: -10px;
  z-index: 5;
}

.headerCitySelect-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  height: max-content;
  max-height: 120px;
  overflow-y: auto;
}

.headerCitySelect-wrapper::-webkit-scrollbar {
  width: 27px;
}

.headerCitySelect-wrapper::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px 10px #808080;
  box-shadow: inset 0 0 10px 10px #808080;
  border: solid 12px transparent;
}

.headerCitySelect-wrapper::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 10px 10px #141413;
  box-shadow: inset 0 0 10px 10px #141413;
  border: solid 10px transparent;
}

.headerCitySelect-element {
  width: 100%;
  padding: 3px 0;
  background: 0 0;
  color: #141413;
  border: none;
  margin: 0 0 5px 0;
  text-align: left;
  cursor: pointer;
}

.headerCitySelect-element-active {
  background: #ffffff;
}

.headerCitySelect-item-active::after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.headerCitySelect-item-active .headerCitySelect-block {
  padding: 20px;
  width: 100px;
  height: auto;
  border-radius: 30px;
  background-color: #ffffff;
  border: 1px solid #141413;
}

.footer {
  background-color: #201547;
  width: 100%;
  max-height: 630px;
  height: max-content;
  padding: 55px 0 65px 0;
  color: #ffffff;
}

.footer__container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 40px;
  width: 100%;
}

.footer__block {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 78px;
