}

.footer__phone {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 22px;
}

.footer__phone a {
  font-weight: 700;
  font-size: 26px;
  line-height: 21px;
  color: #ffffff;
}

.footer__phone p {
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
}

.footer__social {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 20px;
}

.footer__social-title {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-transform: uppercase;
}

.footer__social-list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
}

.footer__social-item {
  width: 47px;
  height: 47px;
  border-radius: 50%;
  background: #ffffff;
}

.footer__social-item a {
  width: 47px;
  height: 47px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 20px;
}

.footer__list-item a {
  color: #ffffff;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
}

.footer__contact {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 40px;
}

.footer__contact-button {
  background: #f2cd00;
  border-radius: 10px;
  padding: 23px 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 35px;
  cursor: pointer;
}

.footer__contact-button p {
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #141413;
}

.footer__contact-list {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 20px;
}

.footer__contact-item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
}

.footer__contact-item p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}

.indexMain {
  width: 100%;
  height: calc(100vh - 70px);
  position: relative;
}

.indexMain__containers {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
}

.indexMain__containers-left {
  width: 50%;
  height: calc(100vh - 70px);
  background-color: #201547;
}

.indexMain__containers-right {
  width: 50%;
  height: calc(100vh - 70px);
  overflow: hidden;
  position: relative;
}

.indexMain__containers-right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.indexMain__containers-right::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #ffffff;
  opacity: 0.8;
}

.indexMain__slider {
  position: relative;
  z-index: 2;
  height: calc(100vh - 70px);
  width: 100%;
}

.indexMain__slider .swiper {
  height: calc(100vh - 70px);
}

.indexMain__left {
  width: 50%;
  height: calc(100vh - 70px);
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding-top: 90px;
  position: relative;
}

.indexMain__left::after {
  content: "";
  position: absolute;
  top: 40%;
  left: 0;
  width: 114px;
  height: 2px;
  background-color: #f2ce14;
  transform: translate(0, -40%);
}

.indexMain__left-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
}

.indexMain__left-block {
